.contact__item {
  @include middle;
  margin-bottom: 11px;
}

.contact__item--point-adress {
  background-image: url("../img/sprite/map-point.svg");
  background-size: 13px 16px;
  background-repeat: no-repeat;
  cursor: pointer;
  padding-left: 19px;
}

.contact__item--phone {
  background-image: url("../img/sprite/phone.svg");
  background-size: 13px 16px;
  background-repeat: no-repeat;
  cursor: pointer;
  padding-left: 19px;

  a {
    &:hover,
    &:focus {
      color: $primary-lighter;
    }

    &:active {
      color: $primary-darker;
    }
  }
}
