.catalog__form .button {
  width: 280px;
  gap: 20px;
  margin-top: -5px;
  margin-left: 3px;
  margin-bottom: 37px;
}

.button-next {
  &:focus,
  &:hover {
    background: linear-gradient(90deg, rgba(255, 255, 255, 0), #ffffff 100%);
  }

  &:active {
    background: linear-gradient(90deg, rgba(120, 89, 207, 0), rgba(120, 89, 207, 0.25) 100%);
  }

  &:default {
    color: $base-grey;
  }
}

.button-previous {
  &:focus,
  &:hover {
    background: linear-gradient(90deg, #ffffff, rgba(255, 255, 255, 0) 100%);
  }

  &:active {
    background: linear-gradient(90deg, rgba(120, 89, 207, 0.25), rgba(120, 89, 207, 0) 100%);
  }

  &:default {
    color: $base-grey;
  }
}

.button__apply {
  @include global;
  font-weight: 400;
  padding: 8px;
  border: none;
  border-radius: 4px;
  background-color: $base-primary;
  color: $base-white;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $primary-lighter;
  }

  &:active {
    background-color: $primary-darker;
  }

  &:disabled {
    background-color: $base-grey;
  }

  @media (min-width: $tablet-width) {
    padding: 8px 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 8px 27px;
  }
}

.button__throw-off {
  @include global;
  padding: 8px;
  border: none;
  border-radius: 4px;
  background-color: $base-white;
  color: $primary-lighter;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $primary-lighter;
    color: $base-white;
  }

  &:active {
    background-color: $primary-darker;
    color: $base-white;
  }

  &:default {
    background-color: $base-grey;
    color: $base-white;
  }

  @media (min-width: $tablet-width) {
    padding: 8px 20px;
  }

  @media (min-width: $desktop-width) {
    padding: 8px 34px;
  }
}

.button__basket {
  @include middle;
  color: $base-white;
  padding: 0 0 0 26px;
  background-image: url("../img/sprite/basket-button.svg");
  background-size: 20px 16px;
  background-repeat: no-repeat;
  background-position: right 80px center;
  cursor: pointer;
}
