.header {
  min-height: 60px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: $base-white;

  @media (min-width: $tablet-width) {
    height: 80px;
  }

  @media (min-width: 1600px) {
    max-width: 1440px;
    margin: 0 auto;
  }
}

.header__logo {
  padding: 12px 20px;

  @media (min-width: $tablet-width) {
    padding: 22px 20px;
    margin-right: -15px;
  }

  @media (min-width: $desktop-width) {
    display: inherit;
    gap: 20px;
    padding: 22px 40px;
  }

  &:hover,
  &:focus {
    opacity: 0.5;
  }

  img {
    @media (min-width: $tablet-width) {
      width: 82px;
      height: 36px;
    }

    @media (min-width: $desktop-width) {
      width: 170px;
    }
  }
}

.header__logo-text {
  @media (max-width: $tablet-width-only) {
    display: none;
  }

  @media (min-width: $desktop-width) {
    @include small;
    font-weight: 400;
    max-width: 126px;
    text-align: left;
  }
}
